export class AppApi {
    constructor(baseApi) {
        this._baseApi = baseApi;
    }
    getProducts() {
        return this._baseApi.get(`/product/`).then((result) => result.items);
    }
    placeOrder(data) {
        return this._baseApi.post(`/order`, data, 'POST').then((result) => result);
    }
}
