import { Form } from "../base/Form";
export class ContactsPreview extends Form {
    constructor(container, events) {
        super(container, events);
        this.container.addEventListener('submit', (e) => {
            e.preventDefault();
            this.events.emit(`success:open`);
        });
    }
    set email(value) {
        this.container.elements.namedItem('email').value = value;
    }
    set phone(value) {
        this.container.elements.namedItem('phone').value = value;
    }
}
