export class FormData {
    constructor(events) {
        this.formErrors = {};
        this.events = events;
        this._orderData = {
            payment: '',
            address: '',
            phone: '',
            email: '',
            items: [],
            total: 0
        };
    }
    get orderData() {
        return this._orderData;
    }
    setOrderField(field, value) {
        this._orderData[field] = value;
        console.log(this._orderData);
        if (this.validateOrder()) {
            return;
        }
    }
    validateOrder() {
        const errors = {};
        if (!this._orderData.address) {
            errors.address = 'Необходимо указать адрес';
        }
        if (!this._orderData.payment) {
            errors.payment = 'Необходимо выбрать способ оплаты';
        }
        this.formErrors = errors;
        this.events.emit('orderForm:change', this.formErrors);
        return Object.keys(errors).length === 0;
    }
    setContactsField(field, value) {
        this._orderData[field] = value;
        if (this.validateContacts()) {
            return;
        }
    }
    validateContacts() {
        const errors = {};
        if (!this._orderData.email) {
            errors.email = 'Необходимо указать email';
        }
        if (!this._orderData.phone) {
            errors.phone = 'Необходимо указать номер телефона';
        }
        this.formErrors = errors;
        this.events.emit('contactsForm:change', this.formErrors);
        return Object.keys(errors).length === 0;
    }
    clearForm() {
        this._orderData = {
            payment: '',
            address: '',
            phone: '',
            email: '',
            items: [],
            total: 0
        };
    }
}
