import { ensureElement } from "../../utils/utils";
import { Component } from "../base/Component";
export class ProductBasket extends Component {
    constructor(container, events) {
        super(container);
        this.events = events;
        this.productTitle = ensureElement('.card__title', this.container);
        this.productPrice = ensureElement('.card__price', this.container);
        this._index = ensureElement('.basket__item-index', this.container);
        this.button = ensureElement('.basket__item-delete', this.container);
        this.button.addEventListener('click', () => {
            this.events.emit('productInBasket:delete', { id: this.productId });
        });
    }
    set id(id) {
        this.productId = id;
    }
    set title(title) {
        this.setText(this.productTitle, title);
    }
    set price(price) {
        this.setText(this.productPrice, `${price.toLocaleString('ru-RU')} синапсов`);
    }
    set index(value) {
        this.setText(this._index, value.toString());
    }
}
