import { createElement, ensureElement } from "../../utils/utils";
import { Component } from "../base/Component";
export class BasketPreview extends Component {
    constructor(container, events) {
        super(container);
        this.events = events;
        this.button = ensureElement('.basket__button', this.container);
        this.button.addEventListener('click', () => {
            this.events.emit('order:place');
        });
        this._total = ensureElement('.basket__price', this.container);
        this._content = ensureElement('.basket__list', this.container);
    }
    set content(items) {
        this._content.replaceChildren(...items);
    }
    set total(value) {
        this.setText(this._total, `${value.toLocaleString('ru-RU')} синапсов`);
    }
    set isEmpty(value) {
        if (value) {
            this._content.replaceChildren(createElement('p', {
                textContent: 'Корзина пуста'
            }));
            this.setDisabled(this.button, true);
        }
        else {
            this.setDisabled(this.button, false);
        }
    }
}
