import { ensureElement } from "../../utils/utils";
import { Component } from "../base/Component";
export class Page extends Component {
    constructor(container, events) {
        super(container);
        this.container = container;
        this.events = events;
        this.basketButton = ensureElement('.header__basket');
        this.pageWrapper = ensureElement('.page__wrapper');
        this._basketCounter = ensureElement('.header__basket-counter');
        this.basketButton.addEventListener('click', () => this.events.emit('basket:open'));
    }
    set content(items) {
        this.container.replaceChildren(...items);
    }
    set basketCounter(value) {
        this.setText(this._basketCounter, value.toString());
    }
    set locked(value) {
        this.toggleClass(this.pageWrapper, 'page__wrapper_locked', value);
    }
}
